.ol{
    margin-top: 30px;
}

.li{
    padding-left: 0px;
    text-align: left;
    font-size: 20px;
    font-weight: 500;
    min-height: 40px;
    display: flex;
    align-items: center;
    color: #8566aa;
    text-transform: capitalize;
}

.toDo_Style{
    display: flex;
    flex-direction: row;
    justify-content: left;
    align-items: center;
}

.toDo_Style .fa-times{
    width: 20px;
    height: 20px;
    display: flex;
}